@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  /* stylelint-disable-line */
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  /* stylelint-disable-line */
  font-display: swap;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Colors */
:root {
  --page-background: #ffffff;
  --page-content-background: #f1f1f1;
  --hero-background: #f9f9f9;
  --dark: #000000;
  --dark-01: #0000001a;
  --dark-03: #0000004d;
  --semi-dark: #444444;
  --semi-dark-01: #4444441a;
  --semi-dark-03: #4444444d;
  --dark-action: #231f20;
  --neutral: #ececec;
  --neutral-light: #e5e5e5;
  --neutral-dark: #6c6c6c;
  --light: #ffffff;
  --light-03: #ffffff4d;
  --light-09: #ffffffe6;
  --accent-color: #63d1bb;
  --accent-color-dark: #56c3b2;
  --accent-color-action: #59d5bc;
  --accent-color-action-dark: #46c1ae;
  --accent-color-action-dark-01: #46c1ae1a;
  --opacity-hover: 0.6;
  --opacity-active: 0.3;
  --icon-opacity: 0.2;
  --icon-opacity-hover: 1;
  --icon-opacity-active: 0.3;
  --modal-shadow: 0px 5px 10px #00010140;
  --card-shadow-hover: 0px 6px 10px #0000001a;
  --card-shadow-hover-right: 6px 2px 14px -3px #0000001a;
  --card-shadow-active: 0px 3px 5px #0000001a;
  --transition: 120ms ease-in-out;
  --max-width-big: 1440px;
  --page-padding-big: max(50px, calc((100vw - var(--max-width-big)) / 2));
  /* Может все паддинги уже сюда перенести? */
  --padding-small: 30px;
  --padding-medium: 50px;
  --padding-big: 50px;
  --padding-left-big: 16.67%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: var(--semi-dark);
  background-color: var(--page-background);
  overflow-x: hidden;
  /* Убирает горизонтальную прокрутку от псевдика, который шириной 100vw */
}

.page__container {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
}

.page__title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 178px;
  margin: 0;
  padding: 15px var(--padding-small) 20px;
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  background-color: var(--page-content-background);
}
@media (min-width: 768px) {
  .page__title {
    min-height: 161px;
    padding-top: 0;
    padding-bottom: 38px;
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .page__title {
    min-height: 192px;
    padding-bottom: 73px;
    padding-left: var(--page-padding-big);
    padding-right: var(--page-padding-big);
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 320px) {
  .page__title::after {
    content: "";
    position: absolute;
    bottom: -12px;
    display: none;
    width: 100%;
    height: 24px;
  }
}
@media (min-width: 768px) {
  .page__title::after {
    display: block;
    background: url("../img/hero-divider-medium.svg") repeat-x center;
  }
}
@media (min-width: 1150px) {
  .page__title::after {
    display: block;
    height: 34px;
    background: url("../img/hero-divider-big.svg") repeat-x center;
  }
}

.container {
  position: relative;
  padding-left: var(--padding-small);
  padding-right: var(--padding-small);
}
@media (min-width: 768px) {
  .container {
    padding-left: var(--padding-medium);
    padding-right: var(--padding-medium);
  }
}
@media (min-width: 1150px) {
  .container {
    position: static;
    padding-left: var(--page-padding-big);
    padding-right: var(--page-padding-big);
  }
}
@media (min-width: 320px) {
  .container--full-width-small {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 768px) {
  .container--full-width-small {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1150px) {
  .container--full-width-small {
    position: static;
    padding-left: var(--page-padding-big);
    padding-right: var(--page-padding-big);
  }
}

.button {
  display: flex;
  justify-content: center;
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--main-font-color);
  background-color: transparent;
  border: 2px solid var(--semi-dark);
  cursor: pointer;
}
.button:hover {
  color: var(--light);
  background-color: var(--semi-dark);
}
.button:active {
  color: var(--light-03);
  background-color: var(--semi-dark);
}
.button--disabled {
  opacity: 0.2;
  cursor: auto;
}
.button--disabled:hover {
  color: var(--main-font-color);
  background-color: transparent;
  opacity: 0.2;
  cursor: auto;
}
.button--disabled:active {
  color: var(--main-font-color);
  background-color: transparent;
  opacity: 0.2;
  cursor: auto;
}
.button--select {
  color: var(--semi-dark);
  background-color: transparent;
  border: 2px solid var(--semi-dark-01);
}
.button--select:hover {
  color: var(--semi-dark);
  background-color: transparent;
  border: 2px solid var(--semi-dark-03);
}
.button--select:active {
  color: var(--semi-dark);
  background-color: transparent;
  border: 2px solid var(--accent-color);
}
.button--accent {
  color: var(--light);
  background-color: var(--accent-color);
  border: 2px solid var(--accent-color);
}
@media (min-width: 768px) {
  .button {
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 17px;
  }
}

.toggle {
  width: 30px;
  height: 24px;
  padding: 5px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.toggle:hover {
  opacity: var(--opacity-hover);
}
.toggle:active {
  opacity: var(--opacity-active);
}
.toggle--close {
  background-color: transparent;
}

.toggle__button {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  background: linear-gradient(#000000 2px, #ffffff 2px, #ffffff 6px, #000000 6px, #000000 8px, #ffffff 8px, #ffffff 12px, #000000 12px, #000000 14px);
  transition: var(--transition);
}
.toggle__button::before {
  content: "";
  display: block;
  width: 20px;
  height: 4px;
  background-color: var(--page-background);
  opacity: 0;
  transition: var(--transition);
}
.toggle__button::after {
  content: "";
  display: block;
  width: 20px;
  height: 4px;
  background-color: var(--page-background);
  opacity: 0;
  transition: var(--transition);
}
.toggle__button--close {
  position: relative;
  background: none;
  transform-origin: center center;
  transform: rotate(45deg);
  transition: var(--transition);
  transition-delay: 0.15s;
}
.toggle__button--close::before {
  content: "";
  position: absolute;
  transform: translateY(-50%) rotate(90deg);
  top: 50%;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--dark-action);
  opacity: 1;
  transition-delay: 0.15s;
}
.toggle__button--close::after {
  content: "";
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  display: block;
  width: 100%;
  height: 2px;
  opacity: 1;
  background-color: var(--dark-action);
}

.logo {
  display: block;
  background-color: transparent;
}
.logo:hover {
  opacity: var(--opacity-hover);
}
.logo:active {
  opacity: var(--opacity-active);
}
.logo--footer {
  color: var(--dark);
}
.logo--footer:hover {
  color: var(--accent-color-dark);
}
.logo--footer:active {
  color: var(--accent-color-dark);
  opacity: var(--opacity-active);
}

.slider {
  padding: 12px 0 45px;
}
@media (min-width: 768px) {
  .slider {
    display: grid;
    grid-template-columns: 73.8% 26.2%;
    grid-template-rows: auto auto;
    padding: 80px 0 137px;
  }
}
@media (min-width: 1150px) {
  .slider {
    padding: 75px 0 max(186px, 18%) var(--padding-left-big);
  }
}
.slider::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: url("../img/reviews-background.jpg") no-repeat left top transparent;
}
@media (min-width: 320px) {
  .slider::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .slider::before {
    display: block;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .slider::before {
    background-image: url("../img/reviews-background@2x.jpg");
    background-size: 350px auto;
  }
}

.slider__title {
  margin: 0;
  margin-bottom: 14px;
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: var(--dark);
}
@media (min-width: 768px) {
  .slider__title {
    grid-column: 1/2;
    grid-row: 1/2;
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 39px;
  }
}
@media (min-width: 1150px) {
  .slider__title {
    margin-bottom: 35px;
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 53px;
    line-height: 57px;
  }
}

.slider__list {
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .slider__list {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin: 0;
  }
}

.slider__item {
  display: none;
}
.slider__item--current {
  display: block;
}

.slider__content {
  margin: 0;
}

.slider__controls {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .slider__controls {
    grid-column: 2/3;
    grid-row: 2/3;
    align-self: end;
    justify-self: end;
    width: 100%;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .slider__controls {
    max-width: 173px;
  }
}

.slider__control {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  padding-top: 13px;
  padding-bottom: 13px;
  background-color: transparent;
  border: 1px solid var(--neutral);
  cursor: pointer;
}
.slider__control--prev {
  transform: rotate(180deg);
  border-left: none;
}
.slider__control:hover {
  opacity: var(--opacity-hover);
}
.slider__control:active {
  opacity: var(--opacity-active);
}
@media (min-width: 768px) {
  .slider__control {
    border: none;
  }
}

@media (min-width: 768px) {
  .slider__button {
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: start;
    justify-self: end;
    height: auto;
    margin-top: -6px;
    /* Не могу сообразить, как сделать так, чтобы крышка грида строилась по этому элементу. */
    padding-left: 26px;
    padding-right: 26px;
  }
}
@media (min-width: 1150px) {
  .slider__button {
    margin-top: 0;
  }
}

.map {
  position: relative;
}

.map__link {
  position: absolute;
  z-index: -1;
  display: block;
  width: 100%;
}

.map__image {
  width: 100%;
  max-height: 455px;
  margin: 0 auto;
  object-fit: contain;
}

.map__body {
  width: 100vw;
  height: 455px;
}
@media (min-width: 1150px) {
  .map__body {
    width: 100%;
    height: 457px;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 29px;
}

.social__img {
  color: var(--dark-action);
}
.social__img:hover {
  color: var(--accent-color-action-dark);
}
.social__img:active {
  color: var(--accent-color-action-dark);
  opacity: var(--opacity-active);
}

.review__text {
  margin: 0;
  margin-bottom: 33px;
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .review__text {
    margin-bottom: 61px;
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 30px;
    text-shadow: 0 0 2px #ffffffbf;
    /* Эффекта не вижу. Возможно потому что фон вставлен через позиционированный псевдоэлемент. */
  }
}
@media (min-width: 1150px) {
  .review__text {
    margin-bottom: 46px;
  }
}

.review__author {
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: var(--dark);
}
@media (min-width: 768px) {
  .review__author {
    display: flex;
    flex-direction: column;
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .review__author {
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
  }
}

.review__link {
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 34px;
  text-decoration: none;
  color: var(--semi-dark);
}
.review__link:hover {
  opacity: var(--opacity-hover);
}
.review__link:active {
  opacity: var(--opacity-active);
}
@media (min-width: 768px) {
  .review__link {
    font-family: "Open Sans", Arial, sans-serif;
    /* stylelint-disable-line */
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 30px;
  }
}

.copyright__link {
  display: flex;
  align-items: center;
  gap: 27px;
  text-decoration: none;
}
.copyright__link:hover .copyright__img {
  color: var(--accent-color-dark);
}
.copyright__link:active .copyright__img {
  color: var(--accent-color-dark);
  opacity: var(--opacity-active);
}

.copyright__text {
  font-family: "Open Sans", Arial, sans-serif;
  /* stylelint-disable-line */
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: var(--dark);
}

.copyright__img {
  color: var(--accent-color);
}

.video {
  position: relative;
  width: 100%;
  aspect-ratio: 26/15;
}
@media (min-width: 768px) {
  .video {
    aspect-ratio: 37/25;
  }
}
@media (min-width: 1150px) {
  .video {
    aspect-ratio: 37/25;
  }
}
.video:hover .video__icon {
  transform: scale(1.2);
}
.video:active .video__icon {
  transform: scale(0.8);
  opacity: var(--opacity-active);
}

.video__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--neutral);
}

.video__icon {
  transition: var(--transition);
}

.video__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video__play {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: block;
  width: 101px;
  height: 77px;
  color: var(--accent-color);
  background-color: transparent;
  border: none;
  transform: translate(-50%, -50%);
  cursor: pointer;
  pointer-events: none;
}

.video__icon-background {
  color: var(--light);
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.main-header {
  position: relative;
  background-color: var(--page-background);
}
@media (min-width: 320px) {
  .main-header {
    min-height: 81px;
    padding: 0;
  }
}
@media (min-width: 768px) {
  .main-header {
    padding: 0;
    border: none;
  }
}
@media (min-width: 1150px) {
  .main-header {
    padding: 0;
  }
}

@media (min-width: 320px) {
  .main-header__navigation--open {
    position: absolute;
    /* Блин, это нарушение БЭМа! */
    left: 0;
    right: 0;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .main-header__navigation--open {
    position: static;
  }
}
@media not all and (min-width: 768px) {
  .main-header__navigation--close .main-header__menu {
    display: none;
  }
}

.main-header__toggle {
  display: none;
}
.main-header__toggle--open {
  display: block;
}

.main-navigation {
  display: grid;
  grid-template-columns: 25% 50% 25%;
  grid-template-rows: 81px auto;
  border-bottom: 1px solid var(--neutral);
}
@media (min-width: 768px) {
  .main-navigation {
    display: flex;
    flex-wrap: wrap;
    min-height: 89px;
    border-bottom: none;
  }
}

.main-navigation__logo {
  position: relative;
  height: 35px;
}
@media (min-width: 320px) and (max-width: 768px) {
  .main-navigation__logo {
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: center;
    justify-self: center;
  }
}
@media (min-width: 768px) {
  .main-navigation__logo {
    justify-self: start;
    display: flex;
    align-items: center;
    align-self: stretch;
    flex-basis: 30%;
    height: auto;
    min-height: 91px;
    padding-left: var(--padding-medium);
  }
}
@media (min-width: 1150px) {
  .main-navigation__logo {
    order: 2;
    flex-basis: 20%;
    max-width: 20%;
    justify-content: center;
    padding: 0;
  }
}

@media (min-width: 768px) {
  .main-navigation__logo::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 1px;
    margin-left: -var(--padding-medium);
    background-color: var(--neutral);
  }
}
@media (min-width: 1150px) {
  .main-navigation__logo::after {
    content: "";
    margin: 0;
  }
}

@media (min-width: 320px) {
  .main-navigation__toggle {
    grid-column: 3/4;
    grid-row: 1/2;
    align-self: center;
    justify-self: center;
    margin-right: 2px;
    margin-bottom: 1px;
  }
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

@media (min-width: 320px) {
  .main-navigation__main-menu {
    grid-column: 1/-1;
    background-color: var(--page-background);
  }
}
@media (min-width: 768px) {
  .main-navigation__main-menu {
    order: 2;
    flex-basis: 60%;
    padding-left: var(--padding-big);
    background-color: var(--page-content-background);
  }
}
@media (min-width: 1150px) {
  .main-navigation__main-menu {
    order: 1;
    flex-basis: 40%;
    flex-grow: 1;
    margin-left: -33px;
    background-color: var(--page-background);
    padding-left: var(--page-padding-big);
